import React, { Component } from 'react';
import Menu from '../sub/menu';
import './Header.css';
import logo from '../../images/logo-for-website-1-e1536267500497.png';

export default class Header extends Component {

    login (){
        return (
            this.props.loggedin ? {title:"LOG OUT", link:"/#/"} : {title:"MY COURSES", link:"/#/login"}
        )
    }

    render() {
        return (
            <div className='HeaderContainer'>
              <img className='logo' alt='igniteapath logo' src={logo}></img>
              < Menu
                  menu={[
                      {title:"about", link:"/#/"},
                      {title:'new? start here', link:'/#/'},
                      {title:'masterclasses', link:'/#/'},
                      this.props.loggedin ? {title:"my courses", link:"/#/secure/account/courses"} : {title:"all courses", link:"/#/courses"},
                      {title:'blog', link:'/#/'},
                      { title: 'contact', link: [
                          {title: 'what'},
                          {title: 'where'},
                          {title: 'whenasdasdadasdasd'}
                      ]},
                  ]}
              />
            </div>
        )
    }
}